
.pricing_section {
  padding: 70px 0px;
}


.pricing_section h2 {
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
  font-size: 50px;
  margin: 0;
}

.pricing_wrapper {
  display: flex;
  flex-wrap: wrap;
}
.pricing_wrapper .pricing_item{
  flex-grow: 1;
  width: 33%;
  padding: 20px;
  box-sizing: border-box;
}

.pricing_wrapper .pricing_inner_wrapper {
  border:2px solid #ffa800;
  padding: 50px 20px;
}

.pricing_inner_wrapper .pricing_title {
  color: #ffffff;
  text-align: center;
  border-bottom: 1px solid #ffa800;
  padding-bottom: 20px;
}
.pricing_inner_wrapper .pricing_title span:nth-child(1) {
  font-size: 50px;
  display: block;
}
.pricing_inner_wrapper .pricing_title span:nth-child(2) {
  text-transform: uppercase;
  font-size: 28px;
  font-weight: 300;
}
.pricing_inner_wrapper .pricing_description {
  color: #b8b8b8;
  font-weight: 300;
  font-size: 14px;
  text-align: center;
  padding: 20px 0px;
  min-height: 70px;
}

.pricing_inner_wrapper .pricing_buttons {
   text-align: center;
}

@media(max-width: 650px) {
  .pricing_section {
    padding: 30px 0px;
  }
  .pricing_wrapper .pricing_inner_wrapper {
    border: 2px solid #ffa800;
    padding: 10px 10px;
  }
  .pricing_inner_wrapper .pricing_title span:nth-child(1) {
    font-size: 35px;
  }
  .pricing_inner_wrapper .pricing_title span:nth-child(2) {
    font-size: 20px;
  }
}
@media(max-width: 550px) {
  .pricing_wrapper {
    flex-direction: column;
  }
  .pricing_wrapper .pricing_item {
    width: 100%;
    padding: 10px;
  }
  .pricing_inner_wrapper .pricing_title {
    padding-bottom: 10px;
  }
  .pricing_inner_wrapper .pricing_description {
    font-size: 15px;
    padding: 10px 0px;
    min-height: 0;
  }
  .pricing_section h2 {
    font-size: 35px;
  }
}